<template>
    <div class="goods">
        <Search></Search>
        <List :list="list" dataName='follow'></List>
        <Footer></Footer>
    </div>
</template>
<script>
import Search from '../components/Search'
import List from '../components/List'
import Footer from '../components/Footer'

export default {
      data(){
    return{
      list:[],
      
    }
  },
    components:{
     Search, List,Footer  
    },
     mounted() {
    this.$axios({
      url:"/api/follow",
      params:{_page:1,_limit:8}
    }).then(
      res=>this.list = res.data.data
    )
  },
    
}
</script>
