<template>
  <div class="detail">
       <mt-header title="详情页" fixed>
          <router-link to="/" slot="left">
          <mt-button icon="back" @click='goback'>返回</mt-button>
           </router-link>
        <mt-button icon="more" slot="right"></mt-button>
        </mt-header>
    <h3>{{detail.title}}</h3>
    <p>{{detail.des}}</p>

  </div>
</template>

<style>
  .detail h3{border:1px solid #ddd;margin:10px;line-height: 40px;text-indent: 2rem;margin-top:60px;color:#333}
  .detail p{border:1px solid #ddd;margin:10px;font:12px/24px "";text-indent: 2rem;color:#444}
</style>

<script>
export default {
  name:'detail',
  data(){
    return{
      detail:{}
    }
  },
  methods:{
    
    goback(){
      this.$router.go(-1)
    }
  },
  mounted(){
    let id = this.$route.params.id;
    let dataName = this.$route.query.dataName
    this.$axios({
      url:`/api/${dataName}/${id}`,
      params:{_page:1,_limit:10}
    }).then(
      res=>this.detail = res.data.data
    )
  }
}
</script>
